import React, { Component} from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button, Modal, ModalHeader, ModalBody
, Label, Col, Row} from 'reactstrap';
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { postComment } from '../redux/ActionCreators';
import { Loading } from './LoadingComponent';
import { baseUrl } from'../shared/baseUrl';
import { FadeTransform, Fade, Stagger} from 'react-animation-components';
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length >= len);


function RenderDish({dish}){
  if(dish != null){
      return(
        <FadeTransform in
          transformProps={{
              exitTransform: 'scale(0.5) translateY(-50%)'
          }}>
          <Card>
              <CardImg width="50%" src={baseUrl + dish.image} alt={dish.name} />
              <CardBody>
                  <CardTitle>{dish.name}</CardTitle>
                  <CardText>{dish.description}</CardText>
              </CardBody>

          </Card>
        </FadeTransform>
      );
  }
  else{
    return(
      <div></div>
    );
  }
}
function  RenderComments({comments, postComment, dishId}){
  if(comments!=null)
  return(
      <div className="container">
        
        <h4>Comments</h4>
        <ul className='list-unstyled'>
          <Stagger in>
          {comments.map((comment ,i)=>{
              return(
              <Fade in>
                <div key={comment.id}>
                <li className='mt-4' >{comment.comment}</li>
                <li className='mt-4'>--{comment.author},{new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}</li>

                </div>  
              </Fade>
              )
          })}
          </Stagger>
          </ul>
          <CommentForm dishId={dishId} postComment={postComment} />
    </div>


  );
  else
  return(
<div></div>
  );

}

class CommentForm extends Component {
  constructor(props){
    super(props);
    this.toggleModal = this.toggleModal.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.state={
      isModalOpen:false
    };

  }
  toggleModal() {
    this.setState({
      isModalOpen: !this.state.isModalOpen
    });
  }
  handleSubmit(values){
    this.toggleModal();
    this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
    console.log('Current State is: ' + JSON.stringify(values));
    // event.preventDefault();
  }
  render(){
  return(
    <div>
    <div>
    <Button onClick={this.toggleModal} type="submit" value="submit" className="bg-primary"><span className="fa fa-pencil fa-lg"></span>Submit Comment</Button>
    </div>

    <Modal fade = {false} isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
      <ModalHeader toggle={this.toggleModal}>Submit Comment</ModalHeader>
      <ModalBody>
        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
            <Row className="form-group">
                        <Col md={12}>
                        <Label check>                      
                          Rating
                        </Label>
                        </Col>
                        </Row>
                        <Row className="form-group">
                                      <Col md={12}>
                                          <Control.select model=".rating" name="rating"
                                              className="form-control">
                                              <option>1</option>
                                              <option>2</option>
                                              <option>3</option>
                                              <option>4</option>
                                              <option>5</option>
                                          </Control.select>
                                      </Col>
                  </Row>
                                <Row className="form-group">
                                    <Label htmlFor="author" md={2}>Author</Label>
                                    <Col md={10}>
                                        <Control.text model=".author" id="author" name="author"
                                            placeholder="Author"
                                            className="form-control"
                                            validators={{
                                                required, minLength: minLength(3), maxLength: maxLength(15)
                                            }}
                                            />
                                        <Errors
                                            className="text-danger"
                                            model=".author"
                                            show="touched"
                                            messages={{
                                                required: 'Required',
                                                minLength: 'Must be greater than 3 characters',
                                                maxLength: 'Must be 15 characters or less'
                                            }}
                                        />
                                    </Col>
                                </Row>
                                <Row className="form-group">
                    <Label htmlFor="comment" md={12}>Comment</Label>
                                  <Col md={12}>
                                          <Control.textarea model=".comment" id="comment" name="comment"
                                              rows="6"
                                              className="form-control" />
                                  </Col>
                  </Row>
                  <Row className="form-group">
                                    <Col>
                    <Button type="submit" value="submit" color="primary">Submit</Button>
                    </Col>

                  </Row>
          </LocalForm>
        <LocalForm onSubmit={(values) => this.handleSubmit(values)}></LocalForm>
      </ModalBody>
    </Modal>
    </div>
        )
      }
  }


    
    const DishDetail = (props) => {
      if(props.isLoading){
        return(
          <div className="container">
            <div className="row">
              <Loading />
            </div>
          </div>
        );
      }
      else if(props.errMess){
        return(
          <div className="container">
            <div className="row">
              <h4>{props.errMess}</h4>
            </div>
          </div>
        );
      }
      console.log('Dishdetail Componenent render is invoked')
      const dish = props.dish;
      
    

      if(props.dish != null){
        return(
          <div className="container">
            <Breadcrumb>
              <BreadcrumbItem><Link to ='/menu'>Menu</Link></BreadcrumbItem>
              <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
            </Breadcrumb>
            <div className="col-12">
              <h3>{props.dish.name}</h3>
              <hr />
            </div>
        <div className="row">
          <RenderDish dish = {props.dish} />
          <RenderComments comments={props.comments}
            postComment={props.postComment}
            dishId={props.dish.id}/>
          
        </div>
        
        </div>
        );
      }else{
          <div>

          </div>
      }
      return(<div></div>)
    
}



export default DishDetail;


